import * as React from 'react';
import { CSSWideKeyword } from 'react';

class Video extends React.Component<{
    id: string,
    title: string,
    summary: string,
    image: string,
    onClick: (id: string) => void
}, {}> {

    handleClick() {
        this.props.onClick(this.props.id);
    }

    render() {

        const contentStyle = {
            margin: '4px',
            padding: '0px',
            height: '100px',
            boxShadow: '0px 0px 2px #000',
            borderRadius: '2px',
            overflow: 'hidden' as CSSWideKeyword
        };

        const imageStyle = {
            width: '80px',
            height: '80px',
            padding: '10px',
            display: 'inline-block'
        };

        const textStyle = {
            lineHeight: '50px',
            verticalAlign: 'top',
            display: 'inline-block',
            paddingLeft: '40px'
        };

        const titleStyle = {
            margin: '0px auto'
        };

        return (
            <div style={contentStyle} onClick={() => this.handleClick()}>
                <img style={imageStyle} src={this.props.image}/>
                <div style={textStyle}>
                    <h2 style={titleStyle}>{this.props.title}</h2>
                    <div>{this.props.summary}</div>
                </div></div>
        );
    }

}

export default Video;


