import * as React from 'react';
import { inject } from 'mobx-react';
import VideoDetail from '../component/videoDetail';
import { match } from 'react-router';
import Gratuity from '../component/gratuity';

@inject((store: any, props) => {
    return Object.assign({}, store.videos.current, {
        select: (id: string) => {
            return store.videos.select(id);
        },
        ...props
    });
})
class Video extends React.Component<{
    match: match<{id: string}>,
    id: string,
    bgUrl: string,
    image: string,
    price: number,
    title: string,
    select: (id: string) => void
}, {
    gratuity: boolean
}> {

    state = {
        gratuity: false
    };

    componentWillMount() {
        this.props.select(this.props.match.params.id);
    }

    handleOk() {
        alert('打开支付' + ['标题:' + this.props.title, '$:' + this.props.price].join('\r\n'));
    }

    handleCancel() {
        this.setState({ gratuity: false});
    }

    render() {
        return (
            <div>
                <VideoDetail
                    id={this.props.id}
                    bgUrl={this.props.bgUrl}
                    image={this.props.image}
                    price={this.props.price}
                    onClick={(id: string) => {
                        this.setState({ gratuity: true});
                    }}
                />
                <Gratuity
                    price={this.props.price}
                    tag={this.props.id}
                    avatar={this.props.image}
                    title={this.props.title}
                    isShow={this.state.gratuity}
                    okHandler={
                    () => {
                        this.handleOk();
                    }
                }
                    cancelHandler={
                        () => {
                            this.handleCancel();
                        }
                    }
                />
            </div>
        );
    }

}

export default Video;


