import * as React from 'react';
import { CSSWideKeyword } from 'react';

class Gratuity extends React.Component<{
    price: number,
    tag: string,
    avatar: string,
    title: string,
    isShow: boolean,
    okHandler: (tag: string) => void,
    cancelHandler: (tag: string) => void
}, {}> {

    render() {

        const contentStyle = {
            position: 'fixed' as CSSWideKeyword,
            top: '0px',
            left: '0px',
            width: '100%',
            height: '100%',
            background: 'rgba(0,0,0,0.5)',
            display: this.props.isShow ? 'block' : 'none'
        };

        const avatarStyle = {
            height: '100px',
            width: '100px',
            margin: '0px auto',
            display: 'block',
            marginTop: '-60px',
            borderRadius: '50px',
            boxShadow: '0px -1px 2px #000'
        };

        const redStyle = {
            background: '#EB4654',
            borderRadius: '8px',
            width: '300px',
            margin: '0px auto',
            padding: '10px',
            marginTop: '10%',
            boxShadow: '0px 0px 5px #000',
            position: 'relative' as CSSWideKeyword
        };

        const titleStyle = {
            textAlign: 'center',
            color: '#fff',
            marginTop: '12px'
        };

        const priceStyle = {
            color: '#F0BB77',
            fontSize: '2em',
            textAlign: 'center',
            margin: '0px auto',
            display: 'block'
        };

        const priceSmallStyle = {
            fontSize: '0.5em',
            paddingLeft: '6px'
        };

        const cancelStyle = {
            textAlign: 'center',
            margin: '20px',
            color: '#fff'
        };

        const okStyle = {
            background: '#F0BB77',
            margin: '8px',
            padding: '10px',
            textAlign: 'center',
            borderRadius: '4px',
            color: '#EB4654',
            height: '40px',
            lineHeight: '40px',
            fontSize: '1.2em',
            fontWeight: 'bolder' as CSSWideKeyword,
            cursor: 'pointer'
        };

        const closeStyle = {
            position: 'absolute' as CSSWideKeyword,
            right: '0px',
            top: '0px',
            fontSize: '2em',
            lineHeight: '1em',
            marginTop: '-4px',
            cursor: 'pointer'
        };

        return (
            <div style={contentStyle}>
                <div style={{display: 'table', width: '100%', height: '100%'}}>
                    <div style={{ display: 'table-cell', verticalAlign: 'middle', textAlign: 'center'}}>
                    <div style={redStyle}>
                        <div
                            style={closeStyle}
                            onClick={() => {
                            this.props.cancelHandler(this.props.tag);
                        }}
                        >×
                        </div>
                        <img style={avatarStyle} src={this.props.avatar}/>
                        <h4 style={titleStyle}>{this.props.title}</h4>
                        <strong style={priceStyle}>{this.props.price && this.props.price.toFixed(2)}
                            <small style={priceSmallStyle}>元</small>
                        </strong>
                        <div style={cancelStyle}>
                            <a
                                style={{color: '#fff'}}
                                href="#"
                                onClick={() => {
                                    this.props.cancelHandler(this.props.tag);
                                }}
                            >
                                取消
                            </a>
                        </div>
                        <div
                            style={okStyle}
                            onClick={() => {
                            this.props.okHandler(this.props.tag);
                        }}
                        >
                            赏红包
                        </div>
                    </div>
                    </div>
                </div>
            </div>);

    }

}

export default Gratuity;


