import { action, observable } from 'mobx';
import Video from './video';

class VideoList {

    @observable
    items: Video[] = [
        {
            id: '1',
            title: 'title1',
            summary: 'summary1',
            image:
                'http://www.mnphb.com/images/m_55614c3ef2876.jpg',
            bgUrl: 'bgUrl',
            resource: 'hello1',
            price: 5.00
        },
        {
            id: '2',
            title: 'title2',
            summary: 'summary2',
            image: 'http://www.mnphb.com/images/m_5763e6c2a8fe5.jpg',
            bgUrl: 'bgUrl2',
            resource: 'hello2',
            price: 6.00
        },
        {
            id: '3',
            title: 'title3',
            summary: 'summary3',
            image: 'http://www.mnphb.com/images/m_5566bf0669225.jpg',
            bgUrl: 'bgUrl3',
            resource: 'hello3',
            price: 4.00
        },
    ];

    @observable
    current: Video;

    @action select(id: string) {
        this.current = this.items[this.items.map(x => x.id).indexOf(id)];
    }

}

export default VideoList;


