import * as React from 'react';

class VideoDetail extends React.Component<{
    id: string,
    bgUrl: string,
    image: string,
    price: number,
    onClick: (id: string) => void

}, {}> {

    render() {

        const imageStyle = {
            height: '300px',
            width: '300px',
            border: '1px solid #000',
            display: 'block',
            margin: '0px auto',
            marginTop: '40px'
        };

        return (
            <div>
                <img style={imageStyle} src={this.props.image} onClick={() => this.props.onClick(this.props.id)}/>
            </div>
        );

    }

}

export default VideoDetail;


